import GameState from 'states/GameState';
import PointNClickGameState from 'states/PointNClickGameState.js';
import LoadState from 'states/LoadState';

class Game extends Phaser.Game {

	constructor() {
		super(500, 500, Phaser.AUTO, 'content', null);
		this.state.add('LoadState', LoadState, false);
		this.state.add('GameState', PointNClickGameState, false);
		this.state.start('LoadState');
	}

}

new Game();
